import React from "react";
import logoReact from "../../images/logo.svg";
import "./SectionGoal.scss";
import HeadSection from "../../common/headSection/HeadSection.js";

class SectionGoal extends React.Component {
  render() {
    return (
      <div className="SectionGoal">
        <p>
          <i>
            Jestem absolwentem informatyki o specjalizacji technologie
            internetowe i mobilne. Posiadam wieloletnie dowiadczenie wykonujc
            projekty gwnie jako freelancer. Rnorodno technologii, ktre
            udao mi si pozna pozwoliy na dokonanie wyboru jzyka i narzdzi
            w ktrych chciabym si rozwija. Niewtpliwie jest to jzyk
            JavaScript oraz wszelkie technologie zwizane z tym jzykiem, w
            szczeglnoci zafascynowaa mnie biblioteka React.
          </i>
        </p>

        <img src={logoReact} alt="Logo react" className="App-logo" />
      </div>
    );
  }
}
export default SectionGoal;



